$card-width: 200px;
$card-height: 408px;
$font-family: 'Roboto', sans-serif;
$main-color: #060b35;
$secondary-color: #616070;
$button-bg-color: #00acdc;
$button-text-color: white;

body {
  margin: 0;
  font-family: $font-family;
  color: $main-color;
}

.product-card {
  width: $card-width;
  height: $card-height;
  box-sizing: border-box;
  padding: 32px 16px 16px;
  display: flex;
  flex-direction: column;
  // border: 1px solid #ccc;
  // border-radius: 8px;
  background-color: #fff;
  // align-items: center;

  &__img {
    width: 160px;
    height: 134px;
    margin-bottom: 16px;
  }

  &__title {
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 8px;
  }

  &__code {
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    color: $secondary-color;
    margin-bottom: 16px;
  }

  .product-card__review {
    display: flex;
    align-items: center;
    margin-bottom: 16px;

    &__stars {
        display: flex;

        .product-card__star {
            height: 16px;
            width: 16px;
            background-image: url('../images/star.svg');
            background-repeat: no-repeat;
            background-position: center;
            margin-right: 4px;

            &--active {
                background-image: url('../images/star-active.svg');
            }
        }
    }

    &__reviews {
        font-size: 10px;
        font-weight: 400;
        line-height: 14px;
    }
}

  &__price {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: 16px 0;

    &-label  {
      font-weight: 400;
      color: $secondary-color;
    }

    &-value {
      font-weight: 700;
    }
  }

  &__buy-button {
    width: 166px;
    height: 40px;
    background-color: $button-bg-color;
    color: $button-text-color;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
  }
}

